import React from 'react'
import s from './Skill.module.scss'

const Skill = ({icon,title}) => {
    return (<>
            <div className={s.wrapper}>
                <div className={s.card}>
                    <img src={icon} alt="" className="icon"/>
                    <div className={s.technology}>{title}</div>
                </div>
                <div className={s.info}>
                    <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Ad at culpa doloremque eius, illum iste minima modi, nam pariatur reiciendis saepe sed voluptates. A expedita inventore, magnam nobis nostrum sapiente.</p>
                </div>
            </div>

    </>

    )
}

export default Skill