import React from 'react'
import s from './Header.module.scss'

const Header = () => {
    return (
        <header className={s.header}>
            <div className={s.content}>
                <img src="https://fiverr-res.cloudinary.com/images/q_auto,f_auto/gigs/21760012/original/d4c0c142f91f012c9a8a9c9aeef3bac28036f15b/create-your-cartoon-style-flat-avatar-or-icon.jpg" alt=""/>
                <div className={s.title}>
                    <h2>VOVA NIKITIN</h2>
                    <span>Front-End Developer</span>
                </div>
            </div>

        </header>
    )
}

export default Header